.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 140%;
		transform: translate(-50%, -50%);
		@include max-lg {
			width: 150%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		transform: translate(-70%, -10%) scaleX(-1);
		@include min-lg {
			width: 60%;
		}
		@include min-xl {
			width: 50%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 100%;
		transform: translate(63%, -25%) scaleX(-1);
		@include min-lg {
			width: 60%;
		}
		@include min-xl {
			width: 40%;
		}
	}
	.orn-4 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		transform: translate(0, -50%);
		.image-element {
			@include min-md {
				width: 70%;
			}
			@include min-lg {
				width: 50%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 80%;
		transform: translate(-40%, 60%);
		@include min-md {
			width: 70%;
			transform: translate(-40%, 60%);
		}
		@include min-lg {
			width: 40%;
			transform: translate(-20%, 60%);
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(58%, 60%);
		@include min-md {
			width: 70%;
			transform: translate(40%, 60%);
		}
		@include min-lg {
			width: 40%;
			transform: translate(40%, 50%);
		}
	}
	.orn-7 {
		position: absolute;
		top: 0;
		right: 0;
		width: 100px;
		transform: translate(20%, 25%) scaleX(-1);
		@include min-md {
			width: 120px;
			transform: translate(30%, 15%) scaleX(-1);
		}
		img {
			transform-origin: 85% 50%;
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		@include max-lg {
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.cover-description {
					padding: 2rem;
					text-align: center;
					@include max-lg {
						padding: 2rem 1rem;
					}
					* {
						position: relative;
					}
					.title {
						color: var(--color-dark);
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
