.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 260px;
		transform: translate(-65%, 0) scaleX(-1);
		@include min-md {
			width: 360px;
		}
		img {
			transform-origin: 90% 90%;
		}
		.orn-2 {
			position: absolute;
			top: 100%;
			right: 0;
			width: 260px;
			transform: translate(5%, -10%);
			@include min-md {
				width: 360px;
			}
			.orn-3 {
				position: absolute;
				top: 0;
				left: 0;
				width: 80px;
				transform: translate(70%, -10%) scaleX(-1);
				@include min-md {
					width: 100px;
				}
				img {
					transform-origin: 85% 40%;
				}
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		right: 0;
		width: 340px;
		transform: translate(70%, 10%) scaleX(-1);
		@include min-md {
			width: 420px;
		}
		img {
			transform-origin: 45% 90%;
		}
		.orn-5 {
			position: absolute;
			top: 100%;
			left: 0;
			width: 340px;
			transform: translate(0, -10%) scaleX(-1);
			@include min-md {
				width: 420px;
			}
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 80px;
			transform: translate(-70%, 0);
			img {
				transform-origin: 85% 40%;
			}
		}
	}
	.stream-wrapper {
		position: relative;
		z-index: 5;
		@include max-lg {
			.row {
				align-items: center;
				gap: 2rem;
			}
		}
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 3rem 2rem;
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background: var(--color-primary);
					border-radius: var(--border-radius);
					border-bottom-right-radius: 0;
					border-bottom-left-radius: 0;
					opacity: 0.8;
					content: "";
				}
				@include max-md {
					padding: 2rem 1rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		position: relative;
	}
}
