.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 320px;
		transform: translate(-75%, -10%);
		z-index: 3;
		@include min-md {
			width: 380px;
			transform: translate(-70%, -10%);
		}
		img {
			transform-origin: 45% 90%;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 340px;
		transform: translate(50%, 20%);
		@include min-md {
			width: 420px;
			transform: translate(60%, 20%);
		}
		img {
			transform-origin: 90% 90%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 100%;
		transform: translate(0, -10%);
		@include min-md {
			width: 80%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 50%;
		left: 0;
		width: 100%;
		transform: translate(0, 50%);
		@include min-md {
			.image-element {
				width: 75%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(0, 20%);
		@include min-md {
			.image-element {
				width: 70%;
			}
		}
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		background: var(--color-primary);
		border-radius: var(--border-radius);
		z-index: 5;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
