.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 10%;
		left: 0;
		width: 100%;
		transform: translate(0, 0);
		@include min-md {
			.image-element {
				width: 80%;
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 30%;
		right: 0;
		width: 100%;
		transform: translate(0, 0);
		@include min-md {
			.image-element {
				width: 70%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 20%;
		left: 0;
		width: 100%;
		transform: translate(0, 0);
		@include min-md {
			.image-element {
				width: 70%;
			}
		}
		@include min-lg {
			bottom: 0;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(0, 30%);
		@include min-md {
			.image-element {
				width: 70%;
			}
		}
		@include min-lg {
			.image-element {
				width: 50%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		width: 340px;
		transform: translate(-75%, -40%);
		@include min-md {
			width: 480px;
		}
		@include min-lg {
			width: 580px;
		}
		img {
			transform-origin: 45% 90%;
		}
	}
	.orn-6 {
		position: absolute;
		top: 0;
		right: 0;
		width: 260px;
		transform: translate(75%, -30%);
		@include min-md {
			width: 420px;
			transform: translate(70%, -50%);
		}
		@include min-lg {
			width: 480px;
		}
		img {
			transform-origin: 90% 90%;
		}
		.orn-7 {
			position: absolute;
			top: 100%;
			right: 0;
			width: 340px;
			transform: translate(30%, -10%) scaleX(-1);
			@include min-md {
				width: 420px;
				transform: translate(20%, -25%) scaleX(-1);
			}
			@include min-md {
				width: 480px;
			}
			img {
				transform-origin: 45% 90%;
			}
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 240px;
		transform: translate(-50%, 75%);
		@include min-md {
			width: 320px;
			transform: translate(-20%, 75%);
		}
		@include min-lg {
			width: 480px;
		}
		img {
			transform-origin: 90% 90%;
		}
		.orn-9 {
			position: absolute;
			top: 0;
			right: 50%;
			width: 80px;
			transform: translate(50%, -35%);
			@include min-md {
				width: 100px;
			}
			img {
				transform-origin: 85% 40%;
			}
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 240px;
		transform: translate(40%, 80%) scaleX(-1);
		@include min-md {
			width: 340px;
			transform: translate(20%, 85%) scaleX(-1);
		}
		@include min-lg {
			width: 480px;
		}
		img {
			transform-origin: 90% 90%;
		}
		.orn-11 {
			position: absolute;
			top: 0;
			right: 50%;
			width: 80px;
			transform: translate(50%, -35%);
			@include min-md {
				width: 100px;
			}
			img {
				transform-origin: 85% 40%;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		z-index: 3;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				position: absolute;
				top: 50%;
				left: 50%;
				width: 75%;
				aspect-ratio: 3/4;
				object-fit: cover;
				transform: translate(-50%, -50%);
			}
			.frame-photo {
				position: relative;
				width: 100%;
				height: auto;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem 5rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
