.rsvp-section {
	position: relative;
	padding: 0 0 5rem;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.orn-1 {
		position: relative;
		width: 100%;
		pointer-events: none;
		@include max-md {
			left: 50%;
			transform: translateX(-50%);
			width: 125%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 30%;
		width: 80px;
		transform: translate(50%, 0) scaleX(-1);
		@include min-md {
			width: 100px;
		}
		@include min-xl {
			width: 120px;
			transform: translate(70%, 0) scaleX(-1);
		}
		img {
			transform-origin: 85% 40%;
		}
	}
	.orn-3 {
		position: absolute;
		width: 100%;
		bottom: 0;
		right: 0;
		transform: translate(30%, 30%);
	}
	.rsvp-wrapper {
		position: relative;
		.rsvp-form {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 75%;
			transform: translate(-50%, -50%);
			background: var(--color-primary);
			padding: 6.5rem 2rem;
			@include min-xl {
				padding: 8rem 2rem;
			}
			@include max-xs {
				padding: 5rem 2rem;
			}
			@include max-md {
				width: 90%;
			}
			.title-section {
				&::before {
					all: unset;
				}
			}
			form {
				position: relative;
				z-index: 8;
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
		}
	}
}
