.reminder-wrap {
	position: relative;
	display: block;
	padding: calc(45vh + 1rem) 0;
	background: var(--color-primary);
	@include max-lg {
		padding: calc(30vh + 1rem) 0;
	}
	@include max-md {
		padding: calc(35vh) 0;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 170%;
		transform: translate(-50%, -50%);
	}
	.orn-2 {
		position: absolute;
		top: 3rem;
		right: 0;
		width: 100%;
		transform: translate(0, 50%);
		@include min-lg {
			top: 0;
			transform: translate(0, 20%);
			.image-element {
				width: 70%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 3rem;
		left: 0;
		width: 100%;
		transform: translate(0, -20%);
		@include min-lg {
			.image-element {
				width: 60%;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 3rem;
		left: 0;
		width: 240px;
		transform: translate(-30%, 0) scaleX(-1);
		@include min-md {
			width: 320px;
			transform: translate(-40%, 0) scaleX(-1);
		}
		@include min-lg {
			width: 420px;
		}
		img {
			transform-origin: 90% 90%;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(65%, 0);
		@include min-md {
			width: 80%;
		}
		@include min-lg {
			width: 60%;
			transform: translate(65%, 10%);
		}
		img {
			transform-origin: 45% 90%;
		}
	}
	.orn-6 {
		position: absolute;
		top: 0;
		right: 30%;
		width: 80px;
		transform: translate(50%, 0) scaleX(-1);
		@include min-md {
			width: 100px;
		}
		@include min-xl {
			width: 120px;
			transform: translate(80%, -10%) rotate(-10deg) scaleX(-1);
		}
		img {
			transform-origin: 85% 40%;
		}
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
